@import "../../global.scss";

.works {
  background-color: rgb(111, 36, 180);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  

  .arrow {
    height: 70px;
    position: absolute;
    cursor: pointer;
    @include mobile{
      height: 50px;
    }
    



    &.left {
    
      left: 100px;
      transform: rotateY(180deg);
      @include mobile{
        left: 10px;
      }
    }

    &.right {
      right: 100px;
      @include mobile{
        right: 10px;
      }
    }
  }

  .slider {
    height: 65%;
    display: flex;
    position: absolute;
    left: 0;
    transition: all 1s ease-out;

  

    .container {
      width: 100vw;
      display: flex;
      align-items: center;
      justify-content: center;

      .item {
        width: 40%;
        height: 100%;
        background-color: white;
        border-radius: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        @include mobile{
          width: 80%;
        }



        .left {
          flex: 6;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
        

          .leftContainer {
            width: 90%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .imgContainer {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              background-color: rgb(245, 179, 155);
              display: flex;
              align-items: center;
              justify-content: center;

         

              img {
                width: 25px;

          
              }
            }
            h2 {
              margin-top: 20px;
              font-size: 30px;
              font-weight: 400;
              @include mobile{
                font-size: 15px;
              }

           
            }
            p {
              font-size: 17px;
              @include mobile{
                font-size: 12px;
              }
            }
      
          }
        }
        .right {
          flex: 6;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          overflow: hidden;

          

          img {
            width: 100%;
            
          }
        }
      }
    }
  }
}
