@import "../../global.scss";
.intro{
    
    background-color: rgb(255, 255, 255);
    display: flex;


    @include mobile{
        flex-direction: column;
        align-items: center;
    }

    .left{
        flex:0.5;
        overflow: hidden;
        
        .imgContainer{
            width: 1100px;
            height: 1100px;
            background-color: rgb(106, 9, 136);
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;

            @include mobile{
                align-items: flex-start;

            }

            img{
            height: 100%;  
            float: right;
            margin-left: 25%;

            

            @include mobile{
                height: 40%;
                margin-left:40%;
                

            }
            @include minMobile{
                margin-left: 20%;
            }
                
            }
        }
    }
    .right{
        flex:0.5;
        position:relative;
        margin-left: 5%;

        .wrapper{
            width: 100%;
            height: 100%;
          
            display: flex;
            flex-direction: column;
            justify-content: center;


            @include mobile{
                padding-left: 0;
                align-items: center;

            }
        }

        h1{
            font-size: 70px;
            margin: 10px 0;
            @include mobile{font-size: 40px;}
             
        }
        h2{
            font-size: 45px;
        }
        h3{
            font-size: 40px;
            @include mobile{font-size: 25px;}

            span{
                font-size: inherit;
                color: rgb(189, 22, 240);

            }
       
        }
       
    }
    .icon{
        padding-left: 40%;
        font-size: 80px;
        color: black;
        animation:arrowBlink 2s infinite;

        @include mobile{font-size: 60px;
        padding-left: 0;}
    }
    @keyframes arrowBlink {
        100%{
            opacity: 0;
        }
    }

    a{
        position: absolute;
        bottom: 50px;
        
       

    
    }
}


