.App{
    height: 110vh;

    .sections{
        width: 100%;
        height: calc(100vh - 77px);
        position: relative;
        top: 200px;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        

        > *{
            width: 100vw;
            height: calc(100vh - 77px);
            scroll-snap-align:start;
            scrollbar-width: none;//for firefox
            &::-webkit-scrollbar{
                display: none;
            }
        }
    }
}