@import "../../global.scss";

.portfolio{
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    align-items: center;
    


    h1{
        font-size: 50px;
        @include mobile{font-size: 20px;}
    }
    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;

        @include mobile{flex-wrap: wrap;
        justify-content: center;}
    }


    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        @include mobile{width: 100%;}

        .item{
            width: 300px;
            height: 170px;
            border-radius: 20px;
            border: 1px solid ghostwhite;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all 0.7 ease;
            cursor: pointer;

            @include mobile{width: 120px;
            height: 100px;}
            
            h3{
                position:absolute;
                font-size: 20px;
                @include mobile{
                    font-size: 12px;
                }
            }
            a{
                text-decoration: none;
                color: white;
            }
        
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            z-index: 1;
        }
        &:hover{
            background-color: $mainColor;
            img{
                opacity: 0.2;
                z-index: 0;
            }
        }
    }
}
}